
.swiper-wrapper {
  margin: 0 auto;
  text-align: center;
}
.swiper-pagination-bullet {
  background-color: #fff;
  opacity: 0.5;
}
.swiper-pagination-bullet-active {
  width: 2rem;
  border-radius: 1rem;
  background-color: #fff;
  opacity: 0.9;
}
.v-slider {
  position: relative;
}
.v-slider .swiper-pagination-bullets {
  bottom: 10px;
  left: 0;
  width: 100%;
}
.v-slider .swiper-pagination-bullets .swiper-pagination-bullet {
  margin: 0 5px;
}
.v-slider .swiper-pagination-bullets .swiper-pagination-bullet-active {
  opacity: 1;
  background: #007aff;
}


