
.tab {
  height: 4.4rem;
}
.tab-ul {
  width: 100%;
  height: 4.4rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.tab-ul .tab-item {
  float: left;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
  line-height: 4rem;
  font-size: 14px;
  color: rgba(255,255,255,0.8);
}
.tab-ul .tab-item .tab-link {
  padding-bottom: 5px;
}
.tab-ul .tab-item.router-link-active .tab-link {
  color: #ffcd32;
  border-bottom: 2px solid #ffcd32;
}


